<template>
  <div class="title-wrapper" ref="titleWrapper">
    <h2 class="title" ref="title">
      <slot name="title"></slot>
    </h2>
    <p class="typography" ref="typography"></p>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
    },
  },
  methods: {
    scrollDots() {
      const parent = this.$refs.titleWrapper;
      const title = this.$refs.title;
      const typography = this.$refs.typography;
      const text = title.innerHTML;
      const distance = 300;
      const distanceCalc = (element, sum = 0) => {
        if (element) {
          sum += element.offsetTop;
          return distanceCalc(element.parentElement, sum);
        } else {
          return sum;
        }
      };
      const offsetTopDistance = distanceCalc(parent);
      typography.innerHTML = "•".repeat(text.length);
      
      const calculate = () =>{
        if (window.innerWidth > 768) {
          let result = Math.floor(
            text.length *
              ((window.scrollY -
                (offsetTopDistance - window.innerHeight + distance / 3)) /
                distance) +
              1
          );
          if (result >= 0 && result <= text.length) {
            typography.innerHTML =
              text.slice(0, result) + "•".repeat(text.length - result);
          } else if(result >= text.length){
            typography.innerHTML = text
          }
        }
        else{
          document.removeEventListener("scroll", calculate);
          typography.style.display = 'none'
          title.style.opacity = '1'
        }
      }
      calculate()
      if (window.innerWidth > 768) {
        document.addEventListener("scroll", calculate);
      }
    },
  },
  mounted() {
    this.scrollDots();
    
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.title-wrapper {
  width: 100%;
  position: relative;
  @include adaptivHeightlg(80, 60);
  @include adaptivMarginlg(0, 0, 0, 50, 0, 0, 0, 20);
  display: flex;
  align-items: center;
  @media (min-width: 1921px) {
    margin-bottom: 50px;
  }
  @media (max-width: 768px) {
    @include adaptivMarginmd(0, 0, 0, 40, 0, 0, 0, 40);
  }
}
.title,
.typography {
  position: absolute;
  top: 0;
  left: 0;
  @include adaptiv-fontlg(48, 30);
  font-family: "Helvetica";
  font-weight: bold;
  margin: 0;
  text-transform: uppercase;
  z-index: 1;
  @media (min-width: 1921px) {
    font-size: 40px;
  }
  @media (max-width: 768px) {
    @include adaptiv-fontmd(48, 24);
  }
}

.title {
  opacity: 0;
  @media (max-width: 768px) {
    opacity: 1;
  }
}
.typography {
  @media (max-width: 768px) {
    display: none;
  }
}
</style>