<template>
  <div class="project">
    <div class="container">
      <h3 class="project-title">НАШИ ПРОЕКТЫ</h3>
      <div class="project__content">
        <div class="project__content-items">
          <CentrisCardsHover
            v-for="(project, index) in projectContent.slice(0, 4)"
            :key="index"
            :project="project"
          ></CentrisCardsHover>
        </div>
        <div class="project__content-items" :class="{ hide: !hideCards }">
          <CentrisCardsHover
            v-for="(project, index) in projectContent.slice(4, 8)"
            :key="index"
            :project="project"
          ></CentrisCardsHover>
        </div>
        <button class="project__content-btn" @click="showContent()">
          СМОТРЕТЬ ЕЩЕ
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import CentrisCardsHover from "@/components/CentrisCardsHover";
export default {
  components: {
    CentrisCardsHover,
  },
  data() {
    return {
      hideCards: false,
      viewportWidth: window.innerWidth,
    };
  },
  methods: {
    showContent() {
      this.hideCards = true;
    },
    goToTop() {
      window.scrollTo(0, 0);
    },
  },
  mounted() {
    window.scrollTo(0, 0);
  },
  computed: {
    projectContent() {
      return this.$store.getters.getProjectContent();
    },
  },
  watch: {},
};
</script>
