import { createRouter, createWebHistory } from "vue-router";
import Home from "@/views/Home";
import ProjectsPages from "@/views/ProjectsPages";
import Page404 from "@/views/Page404";

const projectList = 'project-one|project-two|project-three|project-four|project-five|project-six|project-seven|project-eight'

const routes = [
  // {
  //   path: '/',
  //   name: 'home',
  //   component: Home,
  // },
  // {
  //   path: `/project/:id(${projectList})`,
  //   name: 'project',
  //   component: ProjectsPages,
  // },
  {
    path: '/Centris/',
    name: 'home',
    component: Home,
  },
  {
    path: `/Centris/project/:id(${projectList})`,
    name: 'project',
    component: ProjectsPages,
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'error',
    component: Page404,
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
  mode: 'history'
});

export default router;
