<template>
  <router-link
    class="project__content-item"
    :to="{
      name: 'project',
      params: { id: project.id },
    }"
    @click=" goToTop()"
  >
    <div ref="cart">
      <picture class="project__content-item-img">
        <img :src="require('@/assets/images/projects/' + project.img)" alt="" />
      </picture>
      <video
        ref="cartVideo"
        class="project__content-item-video"
        :class="{ active: itemHover }"
        :src="require('@/assets/video/' + project.video)"
        muted
        loop
        playsinline
      ></video>
      <div class="project__content-item_name">
        <span
          class="project__content-item_name-title"
          v-html="project.title"
        ></span>
        <span
          class="project__content-item_name-descr"
          v-html="project.descr"
        ></span>
      </div>
    </div>
  </router-link>
</template>

<script>
export default {
  props: {
    project: Object,
  },
  data() {
    return {
      itemHover: false,
    };
  },
  methods: {
    registerEvent() {
      this.$refs.cart.addEventListener("mouseover", () => {
        this.itemHover = true;
        this.$refs.cartVideo.play();
      });
      this.$refs.cart.addEventListener("mouseout", () => {
        this.itemHover = false;
        this.$refs.cartVideo.pause();
      });
    },
    goToTop() {
      window.scrollTo(0, 0);
    },
  },
  mounted() {
    this.registerEvent();
     window.scrollTo(0, 0);
  },
  computed: {
    pageData() {
      return this.$store.getters.getPageData(this.$route.params.id);
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.project__content {
  width: 100%;
  &-items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    margin-bottom: 20px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
    }
    @media (max-width: 768px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 20px;

      &.hide {
        display: none;
        gap: 20px;
      }
    }
  }
  &-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-items: center;
    flex-direction: column;
    position: relative;
    &:nth-child(5) {
      .project__content-item_name {
        background: linear-gradient(
          1.27deg,
          #161616 -7.96%,
          rgba(22, 22, 22, 0) 90.74%
        );
      }
    }
    &:nth-child(7) {
      .project__content-item_name {
        background: linear-gradient(
          1.27deg,
          #161616 -7.96%,
          rgba(22, 22, 22, 0) 90.74%
        );
      }
    }
    @media (max-width: 768px) {
      &:hover {
        .project__content-item-video {
          display: none;
        }
      }
    }
    &_name {
      width: 100%;
      height: 100%;
      background: linear-gradient(
        359.33deg,
        #161616 6.19%,
        rgba(22, 22, 22, 0) 92.14%
      );

      filter: blur(50%);
      position: absolute;
      left: 0;
      bottom: 0;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      @include adaptivPaddinglg(25, 25, 25, 20, 22, 22, 22, 16);
      @media (max-width: 768px) {
        @include adaptivPaddingmd(20, 20, 40, 20, 10, 10, 30, 16);
      }

      &-title {
        @include adaptiv-fontlg(25, 28);
        @include adaptiv-lineHeightlg(32, 32);
        font-family: "Helvetica";
        text-transform: uppercase;
        font-weight: normal;
        color: #fff;
      }
      &-descr {
        @include adaptiv-fontlg(15, 12);
        @include adaptiv-lineHeightlg(16, 13);
        font-family: "Helvetica-normal";
        text-transform: uppercase;
        font-weight: normal;
        color: #fff;
      }
    }
    &-video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: none;
      &.active {
        display: block;
      }
      @media (max-width: 768px) {
        display: none;
      }
    }
  }
  &-btn {
    display: none;
    @media (max-width: 768px) {
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      @include adaptivWidthmd(422, 181);
      width: 100%;
      @include adaptivHeightmd(65, 46);
      background: #7338d8;
      color: #fff;
      @include adaptiv-fontlg(24, 18);
      @include adaptiv-lineHeightlg(24, 18);
      text-transform: uppercase;
      font-family: "Helvetica-thin";
    }
  }
}
</style>